/* Global Styles */
@import url('https://fonts.googleapis.com/css2?family=Figtree:wght@300;400;600;700&display=swap');

:root {
  /* Colors */
  --dark-bg: #121212; /* Background for the app */
  --grid-bg: #1e1e1e; /* Slightly lighter dark for the grid background */
  --grid-line: #3a3a3a; /* Subtle gray for grid lines */
  --grid-dot: #555555; /* Lighter gray for dots */
  --timeline-past: rgba(0, 255, 255, 0.2); /* Transparent cyan for past timeline */
  --timeline-future: rgba(128, 128, 128, 0.2); /* Transparent gray for future timeline */
  --present-marker: #ff5f5f; /* Bright red for present time marker */

  /* General Spacing */
  --grid-size: 50px;
  --timeline-thickness: 2px;
}

body {
  margin: 0;
  font-family: 'Figtree', sans-serif;
  background-color: var(--dark-bg);
  color: white;
  overflow: hidden; /* Prevent scrollbars */
}

.timeline-wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: var(--dark-bg);
  overflow: hidden;
  cursor: grab;
}

.timeline-wrapper:active {
  cursor: grabbing;
}

/* Grid Styles */
.grid-cell {
  position: absolute;
  width: var(--grid-size);
  height: var(--grid-size);
  background-color: var(--grid-bg);
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  border: 1px solid var(--grid-line); /* Grid lines */
}

.grid-cell::after {
  content: '';
  width: 4px; /* Dot size */
  height: 4px;
  background-color: var(--grid-dot); /* Lighter gray for dots */
  border-radius: 50%; /* Make dots circular */
  display: block;
}

/* Timeline Line Styles */
.timeline-line {
  position: absolute;
  top: calc(50% - var(--timeline-thickness) / 2); /* Center horizontally */
  height: var(--timeline-thickness);
}

.timeline-line.past {
  background-color: var(--timeline-past);
}

.timeline-line.future {
  background-color: var(--timeline-future);
}

/* Present Marker */
.present-marker {
  position: absolute;
  width: 2px;
  height: 100%;
  background-color: var(--present-marker);
  box-shadow: 0 0 15px var(--present-marker);
}

/* Remove unwanted smoothing effects */
.timeline {
  pointer-events: none;
}
